import React, {Component} from "react";
import '../css/project.css';

class Project extends Component {
    render() {
        return (
            <div>
                <a href={this.props.url} target="_blank">
                    <div className="card">
                        <img src={this.props.src}/>
                        <h2>{this.props.title}</h2>
                    </div>
                </a>
            </div>
        );
    }
}

export default Project;